*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: $fz-default;
  line-height: 24px;
  font-family: $ff-inter;
  color: $color-default-black;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  background-color: $color-default-white;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
