// Vendor
@import "vendor/normalize";

// Variables
@import "utils/variables";
@import "utils/mixins";
@import "utils/reboot";
@import "utils/utils";

// Styles
.body {
  max-width: 768px;
  margin: 0 auto;
}

.header {
  padding-top: 20px;
  &__subtitle {
    margin: 0;
    font-size: 16px;
  }
  &__title {
    margin: 0;
    font-size: 24px;
    line-height: 28px;
  }
}

.nav {
  padding-top: 20px;
  &__list {
    display: flex;
    justify-content: start;
    list-style: none;
    padding-left: 0;
    margin: 0;
  }
  &__item {
    padding-left: 0;
    margin-right: 15px;
    border-bottom: 2px solid transparent;
    transition: .15s;
    &:hover {
      cursor: pointer;
      border-bottom: 2px solid black;
    }
  }
}

.main {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  &__title {
    font-size: 16px;
    margin: 5px 0;
  }
  &__block {
    width: 30%;
    margin-top: 20px;
  }
}

#img {
  max-height: 200px;
}

#cover {
  max-height: 200px;
}