// Colors

$color-default-white: #ffffff;
$color-default-black: #000000;
$color-transparent: rgba(255, 255, 255, 0);

$color-overlay: rgba(0, 0, 0, 0.6);

// Font

$fz-default: 18px;
$ff-inter: "Inter", "Arial", sans-serif;

// Viewport
$vp-768: 768px;
$vp-1440: 1440px;

// Animation

$tf-default: ease;
$trans-default: 0.3s $tf-default;
